.App {
  height: 100vh;
  width: 100vw;
  display: flex;
  background: linear-gradient(63deg, #fb69e4, #5712e2);
  background-size: 400% 400%;
  -webkit-animation: AnimationName 5s ease infinite;
  -moz-animation: AnimationName 5s ease infinite;
  animation: AnimationName 5s ease infinite; }

@-webkit-keyframes AnimationName {
  0% {
    background-position: 70% 0%; }
  50% {
    background-position: 31% 100%; }
  100% {
    background-position: 70% 0%; } }

@-moz-keyframes AnimationName {
  0% {
    background-position: 70% 0%; }
  50% {
    background-position: 31% 100%; }
  100% {
    background-position: 70% 0%; } }

@keyframes AnimationName {
  0% {
    background-position: 70% 0%; }
  50% {
    background-position: 31% 100%; }
  100% {
    background-position: 70% 0%; } }

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0; }

.login-container {
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around; }
  .login-container h1 {
    width: 100%;
    color: white;
    text-align: center;
    font-size: 4rem;
    padding-bottom: 10%; }
  .login-container button {
    height: 20%;
    width: 10%;
    border-radius: 50%;
    padding: 20px; }

.create-user-container {
  margin: auto; }
  .create-user-container form label {
    display: grid;
    padding: 3% 0; }
  .create-user-container form button {
    margin: 0 14%; }

.MainContent {
  display: flex;
  width: 100%; }
  .MainContent .active-view {
    background: #5712E2; }

.Training {
  display: grid;
  grid-template-areas: "vocab title . " "vocab filterWord filterWord" "vocab definition definition";
  width: 90%;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: auto 1fr 1fr;
  padding: 3%; }
  .Training .training-title {
    grid-area: title;
    justify-self: center;
    width: 250px;
    padding: 1rem;
    margin-bottom: 5%;
    text-align: center;
    font-size: 3rem;
    background: rgba(0, 0, 0, 0.32);
    border-radius: 50%; }
  .Training .vocab-container {
    grid-area: vocab;
    display: grid;
    grid-template-rows: 1fr 6fr;
    justify-content: space-around; }
    .Training .vocab-container h2 {
      align-self: self-end;
      justify-self: center;
      font-size: 2rem;
      animation: small-size-increase infinite 5s; }

@keyframes small-size-increase {
  0%,
  100% {
    text-shadow: 0px 0px 1px pink; }
  50% {
    text-shadow: 2px 2px 3px pink; } }
    .Training .vocab-container .vocab-list {
      min-width: fit-content;
      overflow: auto;
      list-style-type: none;
      border-radius: 25px;
      padding: 2%;
      background: rgba(0, 0, 0, 0.32); }
      .Training .vocab-container .vocab-list li {
        font-size: 3rem; }
        .Training .vocab-container .vocab-list li:hover {
          cursor: pointer; }
      .Training .vocab-container .vocab-list .selected-word {
        color: red; }

.Filter {
  grid-area: filterWord;
  text-align: center; }
  .Filter h1 {
    height: 10%; }
  .Filter ul {
    list-style-type: none;
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: 90%; }
    .Filter ul li {
      background: rgba(0, 0, 0, 0.32);
      border-radius: 25px;
      justify-self: center;
      font-size: 2rem;
      width: 80%;
      margin: 1%;
      display: flex;
      justify-content: center;
      align-items: center; }
      .Filter ul li:hover {
        cursor: pointer; }

.ToggleDefinition {
  grid-area: definition;
  display: grid;
  padding: 5%;
  justify-items: center;
  margin: 5%;
  border-radius: 25px;
  background: rgba(0, 0, 0, 0.32); }
  .ToggleDefinition .word {
    font-size: 2rem; }
  .ToggleDefinition .definition {
    font-size: 2rem; }

.Menu {
  grid-area: menu;
  display: grid;
  background: radial-gradient(circle, #fb69e4 6%, #5712e2 83%);
  height: 100%;
  min-width: fit-content;
  padding: 1%; }
  .Menu h1 {
    color: rgba(251, 105, 228, 0.6); }
  .Menu ul li {
    display: grid;
    justify-content: center;
    align-content: center;
    height: 4rem;
    margin: 20px 0;
    border-radius: 25px;
    background: radial-gradient(circle, #fb69e4 6%, #5712e2 83%);
    box-shadow: 2px 2px 5px #5712E2; }
    .Menu ul li:hover {
      cursor: pointer; }
  .Menu .back-button {
    position: absolute;
    bottom: -15px;
    left: -10px;
    padding: 35px;
    border-radius: 35px;
    background: rgba(251, 105, 228, 0.6);
    border: none;
    box-shadow: 2px -2px 7px rgba(251, 105, 228, 0.6); }
    .Menu .back-button:hover {
      cursor: pointer; }

.Controller {
  height: 25px;
  width: 25px;
  position: absolute;
  background: red; }

.Profile {
  display: grid;
  text-align: center;
  margin: 10% auto;
  grid-template-areas: ". profile  profile ." "javascript mastery mastery html" ". css turing .";
  grid-template-rows: 1fr 1fr 1fr;
  grid-template-columns: repeat(4, 1fr);
  color: whitesmoke; }
  .Profile .profile-user-info {
    font-size: 2em;
    border-radius: 25px;
    box-shadow: 1px 1px 10px black; }
  .Profile section {
    margin: 10px;
    border-radius: 25px;
    box-shadow: 2px 2px 5px #5712E2;
    display: grid;
    align-content: center;
    justify-items: center; }
  .Profile progress {
    border-radius: 25px;
    background: #5712E2; }
    .Profile progress::-webkit-progress-bar {
      border-radius: 25px;
      background: #5712E2; }
    .Profile progress::-webkit-progress-value {
      border-radius: 25px;
      background: pink; }
  .Profile .animate-info {
    background: radial-gradient(circle, #fb69e4 6%, #5712e2 83%);
    animation: hello 3s infinite;
    max-height: 150px; }
  .Profile .profile-mastery {
    grid-area: mastery;
    font-size: 2rem;
    border: none;
    box-shadow: none; }
  .Profile .profile-user-info {
    grid-area: profile; }
  .Profile .profile-javascript {
    grid-area: javascript; }
  .Profile .profile-css {
    grid-area: css; }
  .Profile .profile-html {
    grid-area: html; }
  .Profile .profile-turing {
    grid-area: turing; }

@keyframes hello {
  0%,
  100% {
    background-size: 100%; }
  50% {
    background-size: 104%; } }

.Game {
  width: 100%; }
  .Game .game-sky {
    display: flex;
    background: lightcyan;
    height: 50%;
    width: 100%; }
    .Game .game-sky .game-status {
      height: 30%;
      padding: 1%;
      text-align: center; }
    .Game .game-sky .game-definition {
      flex: 2;
      font-size: 2rem; }
    .Game .game-sky .game-definition-score {
      flex: 1;
      font-size: 2rem; }
    .Game .game-sky .game-user-score {
      flex: 1;
      font-size: 2rem; }
  .Game .game-ocean {
    position: relative;
    height: 50%;
    width: 100%;
    background: cadetblue; }

.Fish {
  display: flex;
  position: relative;
  width: 150px; }
  .Fish .fish-svg {
    height: 100px; }
  .Fish .fish-word {
    position: absolute;
    top: 40%;
    left: 30%; }
  .Fish:hover {
    cursor: pointer; }
